.site-layout {
  height: 100vh;
}

.content {
  padding: 25px;
}

.site-layout-content {
  height: 100%;
  padding: 24px;
  background: #fff;
}

.new-image-div {
  width: 40vw;
  margin: 0 auto;
}

.sample-list {
  padding: 10px;
  border-radius: 10px;
  background-color: whitesmoke;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sample-list img {
  border-radius: 10px;
  object-fit: cover;
  cursor: pointer;
}

.camera-details-view {
  height: 100vh;
  overflow: scroll;
  padding: 20px;
  scrollbar-width: thin;
}

.metadata-view {
  height: 100vh;
  overflow: scroll;
  padding: 0 30px 0 30px;
  scrollbar-width: thin;
}

.metadata-section {
  margin-bottom: 20px;
}

.metadata-subheading {
  text-align: center;
  margin-bottom: 7px;
}

.curves {
  display: flex;
  flex-direction: column;
  align-items: center;
}
